.carousel {
  display: -ms-flexbox;
  display: flex;
}

.carousel__track {
  -ms-flex: 1 1;
      flex: 1 1;
  white-space: nowrap;
  scroll-behavior: smooth;
  -webkit-scroll-snap-type: x mandatory;
      -ms-scroll-snap-type: x mandatory;
          scroll-snap-type: x mandatory;

  /*
    Using "overflow: scroll;" would be more accessible as user can choose to
    scroll the carousel via mouse/ trackpad. This would need additional
    onScroll handlers to ensure the component's "currentSlideIndex" state is in sync.

    It seems out of the scope of this task, but a nice to consider in real world project.
  */
  overflow: hidden;
  height: 200px;
}

.slide {
  height: 100%;
  display: inline-block;
  padding: 10px;
  scroll-snap-align: start;
}

.slide__content {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid;
  padding: 30px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.btn-navigation {
  font-size: 2rem;
}



