/* Base styles */
body {
  margin: 30px;
  padding: 0;
  font-family: sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #333;
  border-color: #333;
}

* {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

a {
  color: #428ff4;
}

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
}

/* Utility classes */
.btn--bordered {
  border: 1px solid;
  padding: 12px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px; width: 1px;
  margin: -1px; padding: 0; border: 0;
}

.flex-1 {
  -ms-flex: 1 1;
      flex: 1 1;
}



